import React from "react";
import { Grid, TextField, TextareaAutosize } from "@mui/material";
import { Footer, Header } from '../../Component';

const Contact = () => <>
  <Header contact />
  <section className="flex justify-center mt-36 mb-20">
    <div className="w-[60vw] shadow-xl drop-shadow-2xl px-14 py-10 border rounded-xl">
      <h4 className="text-[#FEDD5A] uppercase tracking-[0.3em] text-center">
        Information Desk
      </h4>
      <h1 className="text-4xl mt-8">Get in touch</h1>
      <p className="text-lg mt-4 text-gray-600">
        A member of our team will reach out soon. We look forward to working
        with you!
      </p>
      <form className="mt-10">
        <Grid container spacing={2}>
          <Grid item xs={12} sm={6}>
            <TextField
              autoComplete="given-name"
              name="firstName"
              type="text"
              required
              fullWidth
              id="firstName"
              label="Your first name"
              autoFocus
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              fullWidth
              id="lastName"
              label="Your last name"
              name="lastName"
              type="text"
              autoComplete="family-name"
            />
          </Grid>
          <Grid item container spacing={2} sx={{ mt: "-9px" }}>
            <Grid item xs={12} sm={6}>
              <TextField
                autoComplete="given-email"
                name="email"
                type="email"
                required
                fullWidth
                id="email"
                label="Your email"
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <TextField
                autoComplete="given-number"
                name="phone"
                type="text"
                fullWidth
                id="phone"
                label="Your phone number"
              />
            </Grid>
          </Grid>
          <Grid item xs={12}>
            <TextareaAutosize
              aria-label="Tell us about the services you wanna try"
              name="services"
              placeholder="Tell us about the services you wanna try"
              minRows={8}
              style={{
                width: "100%",
                padding: "15px",
                marginTop: "10px",
                borderRadius: "16px",
                border: "1px solid #ced4da",
                outline: "none",
                "&:focus": { borderColor: "#6c757d" },
              }}
              className="hover:!border-black hover:!border"
              sx={{
                borderRadius: "16px",
                border: "1px solid #ced4da",
                outline: "none",
                "&:focus": { borderColor: "#6c757d" },
              }}
            />
          </Grid>
        </Grid>

        <button className="px-8 py-3 hover:scale-125 transition-transform duration-500 rounded-lg mt-10 bg-[#FFDE59] text-lg">
          Submit
        </button>
      </form>
    </div>
  </section>
  <Footer />
</>

export default Contact;
