import React, { useState } from 'react';
import First from './First';
import { KeyboardBackspace } from '@mui/icons-material';
import { createTheme, IconButton, ThemeProvider } from '@mui/material';
import Step1 from './Step1';
import Step2 from './Step2';

const theme = createTheme({
  components: {
    MuiTextField: {
      styleOverrides: {
        root: {
          '& .MuiFilledInput-root': {
            backgroundColor: '#F7F8FA',
            borderRadius: '8px',
            border: '2px solid transparent',
            '&.Mui-focused': {
              borderColor: '#FFDE59',
            },
            '&:hover': {
              borderColor: '#FFDE59',
            },
          },
          '& .MuiInputLabel-root': {
            color: '#969B9E',
          },
          '& .Mui-focused .MuiInputLabel-root': {
            color: '#FFDE59',
          },
          '& .MuiFilledInput-input::placeholder': {
            color: '#969B9E',
          },
        },
      },
    },
  },
  palette: {
    primary: {
      main: '#FFDE59',
    },
  },
});

const Signup = () => {
  const [first, setFirst] = useState(true);
  const [step1, setStep1] = useState(false);
  const [step2, setStep2] = useState(false);

  return (
    <section className='grid grid-cols-10'>
      {first && <First setFirst={setFirst} setStep1={setStep1} />}
      <ThemeProvider theme={theme}>
        <div className={`col-span-10 flex justify-center py-4 ${!step1 && "hidden"}`}>
          <div className='w-[90vw]'>
            <header className='flex justify-between sticky top-0'>
              <IconButton onClick={() => { setStep1(false); setFirst(true) }}>
                <KeyboardBackspace fontSize='large' />
              </IconButton>
              <h1 className='text-3xl font-semibold w-[35%]'>{step1 ? "Let's get started!" : "Verify your account"}</h1>
              <div className='text-gray-500'>step {step1 ? "1 / 2" : "2 / 2"}</div>
            </header>
            {step1 && <Step1 setStep1={setStep1} setStep2={setStep2} />}
            {step2 && <Step2 />}
          </div>
        </div>
      </ThemeProvider>
    </section>
  );
}

export default Signup;
