import React from "react";
import { luggageManagement } from "../../../assets/arrival";

const LuggageManagement = () => (
  <section className="flex justify-center mt-32 mb-16">
    <div className="w-[85vw] grid grid-cols-2 gap-24 place-items-center">
      <div className="flex justify-start">
        <div className="w-[95%]">
          <img
            src={luggageManagement}
            alt="luggageManagement"
            className="rounded-xl h-[600px]"
          />
        </div>
      </div>
      <div className="">
        <h1 className="text-4xl leading-[3rem] font-medium">
          Seamless Luggage Management for Your Convenience:
        </h1>
        <ul className="mt-9 flex flex-col gap-7">
          {[
            "Enjoy our top-tier baggage transfer and storage services, tailored for VIP travelers.",
            "Stay informed with proactive and real-time communication, ensuring you never miss a detail about your luggage.",
            "Simplify your experience with a dedicated contact, streamlining luggage management across multiple locations.",
            "Benefit from our digitally advanced tools and data, helping you keep track of your luggage with ease.",
            "Access personalized offerings and solutions designed to meet your unique needs.",
          ].map((p, i) => (
            <li key={i} className="flex gap-8 items-center">
              <span className="!h-4 !w-4 bg-[#FFDE59] rounded-full !inline-block" />{" "}
              <p>{p}</p>
            </li>
          ))}
        </ul>
      </div>
    </div>
  </section>
);

export default LuggageManagement;
