import React from "react";
import { Footer, Header } from "../../Component";

const WebsiteAcceptable = () => <>
    <Header />
    <section className="flex flex-col items-center mt-28 mb-20 font-serif">
        <div className="w-[98%] rounded-3xl flex flex-col items-center bg-[#FFDE59] py-20">
            <div>
                <h1 className="text-[#1F1F1F] text-7xl font-extrabold">
                    Website acceptable use policy
                </h1>
                <p className="mt-1 !font-thin">
                    Last updated<span className="font-bold">17th Nov 2023</span>
                </p>
            </div>
        </div>
        <div className="w-[56vw] mt-16">
            <p className="font-thin">
                {
                    "This acceptable use policy sets out our content standards for when you interact with our website in any way, upload content to our site, make contact with other users on our site and/or link to our site. This acceptable use policy is incorporated into Cuckoo’s website terms and conditions by reference."
                }
            </p>
            <div className="mt-4">
                <h1 className="text-3xl font-semibold text-[#1F1F1F]">
                    {"It’s a big NO to any of this..."}
                </h1>
                <div className="mt-4 text-[#1F1F1F] font-thin">
                    <h3 className="text-xl font-bold">You may not use our site:</h3>
                    <ul className="mt-1 list-[circle] flex flex-col gap-3 ml-5">
                        <li>
                            In any way that breaks any relevant local, national or
                            international law or regulation.
                        </li>
                        <li>
                            In any way that is unlawful or fraudulent or has any unlawful or
                            fraudulent purpose or effect.
                        </li>
                        <li>
                            For the purpose of harming or attempting to harm minors in any way.
                        </li>
                        <li>
                            To bully, insult, intimidate or humiliate any person.
                        </li>
                        <li>
                            To send, knowingly receive, upload, download, use or re-use any material which does not comply with our content standards (more info below).
                        </li>
                        <li>
                            To transmit, or aid in the sending of, any unsolicited or unauthorised advertising or promotional material or any other form of similar solicitation (spam).
                        </li>
                        <li>
                            To knowingly transmit any data, send or upload any material that contains viruses, Trojan horses, worms, time-bombs, keystroke loggers, spyware, adware or any other harmful programs or similar computer code designed to adversely affect the operation of any computer software or hardware.
                        </li>
                        <li>
                            In any way that involves child sexual exploitation or abuse. To upload terrorist content.
                        </li>
                    </ul>
                    <h3 className="text-xl font-bold mt-5">You also agree:</h3>
                    <ul className="mt-1 list-[circle] flex flex-col gap-3 ml-5">
                        <li>
                            {"Not to reproduce, duplicate, copy or re-sell any part of our site that goes against Cuckoo’s website terms and conditions."}
                        </li>
                        <li>
                            Not to access without authority, interfere with, damage or disrupt: - any part of our site. - any equipment or network on which our site is stored. - any software used in the provision of our site. - any equipment or network or software owned or used by any third party.
                        </li>
                    </ul>
                </div>
                <div className="mt-4">
                    <h1 className="text-3xl font-bold">
                        Content standards
                    </h1>
                    <div className="mt-5 font-thin">
                        <p>
                            {
                                "These content standards apply to any and all material which you contribute to our site (Contribution) and must be complied with at all times."
                            }
                        </p>
                        <p className="mt-4">
                            {
                                "The standards apply to each part of any Contribution as well as to its whole."
                            }
                        </p>
                        <p className="mt-4">
                            {
                                "Cuckoo will determine, in its discretion, whether a Contribution breaches the Content Standards."
                            }
                        </p>
                        <h3 className="text-xl font-bold">A Contribution:</h3>
                        <ul className="mt-1 list-[circle] flex flex-col gap-3 ml-5">
                            <li>
                                {"Must be accurate (where it states facts)."}
                            </li>
                            <li>
                                Only contain opinions that are genuinely held.
                            </li>
                            <li>
                                Must comply with the law applicable in any country from which it is posted and to which the website is targeted.
                            </li>
                        </ul>
                        <h3 className="text-xl font-bold mt-5">A Contribution must not:</h3>
                        <ul className="mt-1 list-[circle] flex flex-col gap-3 ml-5">
                            <li>
                                Be defamatory of any person.
                            </li>
                            <li>
                                Be obscene, offensive, hateful or inflammatory.
                            </li>
                            <li>
                                Bully, insult, intimidate or humiliate.
                            </li>
                            <li>
                                Encourage, promote or provide instructions for deliberate self-harm.
                            </li>
                            <li>
                                Encourage, promote or provide instructions for suicide.
                            </li>
                            <li>
                                Encourage, promote or provide instructions for an eating disorder or behaviours associated with an eating disorder.
                            </li>
                            <li>
                                Promote sexually explicit material.
                            </li>
                            <li>
                                Include child sexual abuse material.
                            </li>
                            <li>
                                Incite violence or hatred against particular groups.
                            </li>
                            <li>
                                Promote discrimination based on race, sex, religion, nationality, disability, sexual orientation or age.
                            </li>
                            <li>
                                Include content that would be considered a criminal offence under laws relating to terrorism, child sexual abuse material, racism or xenophobia.
                            </li>
                            <li>
                                Infringe any copyright, database right or trade mark of any other person.
                            </li>
                            <li>
                                Include any video content.
                            </li>
                            <li>
                                Include material that might impair the physical, mental or moral development of persons under the age of 18.
                            </li>
                            <li>
                                Be likely to deceive any person.
                            </li>
                            <li>
                                Breach any legal duty owed to a third party, such as a contractual duty or a duty of confidence.
                            </li>
                            <li>
                                Contain illegal content or promote any illegal content or activity.
                            </li>
                            <li>
                                Be in contempt of court.
                            </li>
                            <li>
                                Be threatening, abuse or invade another's privacy, or cause annoyance, inconvenience or needless anxiety.
                            </li>
                            <li>
                                Be likely to harass, upset, embarrass, alarm or annoy any other person.
                            </li>
                            <li>
                                Impersonate any person or misrepresent your identity or affiliation with any person.
                            </li>
                            <li>
                                Give the impression that the Contribution emanates from us, if this is not the case.
                            </li>
                            <li>
                                Advocate, promote, incite any party to commit, or assist any unlawful or criminal act such as (by way of example only) copyright infringement or computer misuse.
                            </li>
                            <li>
                                Contain a statement which you know or believe, or have reasonable grounds for believing, that members of the public to whom the statement is, or is to be, published are likely to understand as a direct or indirect encouragement or other inducement to the commission, preparation or instigation of acts of terrorism.
                            </li>
                            <li>
                                Contain any advertising or promote any services or web links to other sites.
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    </section>
    <Footer />
</>

export default WebsiteAcceptable;
