import React from "react";
import "./index.css"

const DepartureHero = () => (
  <div className="flex justify-center departureBg !w-full">
    <div className="w-[94vw] min-h-screen flex justify-center items-center">
      <div>
        <h4 className="text-[#FEDD5A] uppercase tracking-[0.3em] text-center">
          Departure
        </h4>
        <h1 className="text-[56px] text-black leading-[1] font-bold text-center my-9">
          Never worry about luggage again.
        </h1>
        <p className="text-center text-[#1e1f21] text-2xl font-semibold">
          Save time and hassle while we care for your luggage.
        </p>
        <p className="text-center text-[#1e1f21] text-2xl font-semibold">
          Free pickup and delivery with no hidden fees..
        </p>
        <p className="text-center text-black text-2xl font-semibold">
          Plans start as low as $50 per bag.
        </p>
        <div className="flex justify-center mt-8">
          <button className="bg-[#FEDD5A] text-lg px-7 py-2 rounded-full hover:scale-110 transition-transform duration-500">Get Started</button>
        </div>
      </div>
    </div>
  </div>
);

export default DepartureHero;