import React, { useEffect, useState } from 'react';
import {
    TextField,
    Grid,
    FormControlLabel,
    Checkbox,
    CircularProgress,
    Typography
} from "@mui/material";
import axios from "axios";

const Step2 = ({ setShowList, setStep2, setZipCode, fetchLocations, step1 }) => {
    const [formData, setFormData] = useState({
        Name: "",
        PrimaryContactName: "",
        AddressLine1: "",
        AddressLine2: "",
        City: "",
        State: "",
        PostalCode: "",
        Fax: "",
        Phone: "",
        Email: "",
    });

    const [services, setServices] = useState({
        luggageDeliveryArrival: false,
        luggageDeliveryDeparture: false,
        luggageDeliveryArrivalDeparture: false,
        noLuggageDelivery: false,
        luggageStorageArrival: false,
        luggageStorageDeparture: false,
        luggageStorageArrivalDeparture: false,
        noLuggageStorage: false,
        lostLuggageRetrieval: false,
        noLostLuggageRetrieval: false,
    });
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState('');

    const handleChange = (e) => {
        setFormData({
            ...formData,
            [e.target.name]: e.target.value,
        });
    };

    const handleCheckboxChange = (event) => {
        const { name, checked } = event.target;

        setServices((prevServices) => {
            const newServices = { ...prevServices, [name]: checked };

            // Handle luggage delivery checkboxes
            if (name === 'noLuggageDelivery' && checked) {
                newServices.luggageDeliveryArrival = false;
                newServices.luggageDeliveryDeparture = false;
                newServices.luggageDeliveryArrivalDeparture = false;
            } else if ((name === 'luggageDeliveryArrival' || name === 'luggageDeliveryDeparture' || name === 'luggageDeliveryArrivalDeparture') && checked) {
                newServices.noLuggageDelivery = false;
                if (name === 'luggageDeliveryArrivalDeparture') {
                    newServices.luggageDeliveryArrival = false;
                    newServices.luggageDeliveryDeparture = false;
                } else {
                    newServices.luggageDeliveryArrivalDeparture = false;
                }
            }

            // Handle luggage storage checkboxes
            if (name === 'noLuggageStorage' && checked) {
                newServices.luggageStorageArrival = false;
                newServices.luggageStorageDeparture = false;
                newServices.luggageStorageArrivalDeparture = false;
            } else if ((name === 'luggageStorageArrival' || name === 'luggageStorageDeparture' || name === 'luggageStorageArrivalDeparture') && checked) {
                newServices.noLuggageStorage = false;
                if (name === 'luggageStorageArrivalDeparture') {
                    newServices.luggageStorageArrival = false;
                    newServices.luggageStorageDeparture = false;
                } else {
                    newServices.luggageStorageArrivalDeparture = false;
                }
            }

            // Handle lost luggage retrieval checkboxes
            if (name === 'noLostLuggageRetrieval' && checked) {
                newServices.lostLuggageRetrieval = false;
            } else if (name === 'lostLuggageRetrieval' && checked) {
                newServices.noLostLuggageRetrieval = false;
            }

            return newServices;
        });
    };

    const validateServices = () => {
        const deliverySelected = services.luggageDeliveryArrival || services.luggageDeliveryDeparture || services.luggageDeliveryArrivalDeparture || services.noLuggageDelivery;
        const storageSelected = services.luggageStorageArrival || services.luggageStorageDeparture || services.luggageStorageArrivalDeparture || services.noLuggageStorage;
        const retrievalSelected = services.lostLuggageRetrieval || services.noLostLuggageRetrieval;
        return deliverySelected && storageSelected && retrievalSelected;
    };

    useEffect(() => {
        if (formData?.PostalCode) {
            console.log(formData?.PostalCode);
            setZipCode(formData?.PostalCode);
        }
    }, [formData?.PostalCode, setZipCode]);

    const handleSubmit = async (e) => {
        e.preventDefault();

        if (!validateServices()) {
            setError('Please select at least one option from each category.');
            return;
        }

        fetchLocations()
        setLoading(true);
        setError(''); // Clear any existing error

        try {
            const res = await axios.post(
                "https://secure.ontime360.com/sites/aafcouriers/api/customer/post",
                {
                    ...formData,
                    Country: "United States of America"
                },
                {
                    headers: {
                        "Content-Type": "application/json",
                        Accept: "application/json",
                        Authorization: "4fe1b4ce-9dd4-4375-abb8-31bab61bd149",
                    },
                }
            );
            setShowList(true)
            setStep2({
                ...res.data,
                services
            });
        } catch (error) {
            console.error(error);
        } finally {
            setLoading(false);
        }
    };

    return (
        <div className='relative !mb-2'>
            <form onSubmit={handleSubmit} className='relative'>
                {loading && (
                    <div className='absolute top-0 left-0 h-[98vh] w-[98vw] flex justify-center items-center'>
                        <CircularProgress size={34} style={{ color: '#EB2131' }} />
                    </div>
                )}
                <Grid container spacing={8} className='!w-[80vw] absolute top-0 -right-[20vw]'>
                    <Grid item xs={12} md={6}>
                        <h1 className="text-4xl !font-extrabold text-[#1F1F1F]">
                            Enter Contact Details
                        </h1>
                        <Grid className="!mt-7" container spacing={2}>
                            {Object.keys(formData).map((key, i) => (
                                <Grid key={i} item xs={12} sm={key === 'AddressLine1' || key === 'AddressLine2' || key === 'Phone' || key === 'Email' ? 12 : 6}>
                                    <TextField
                                        fullWidth
                                        label={key.replace(/([A-Z])/g, ' $1').trim()}
                                        id={key}
                                        name={key}
                                        value={formData[key]}
                                        onChange={handleChange}
                                    />
                                </Grid>
                            ))}
                        </Grid>
                    </Grid>
                    <Grid item xs={12} md={6}>
                        <h1 className="text-4xl !font-extrabold text-[#1F1F1F]">Select Service</h1>
                        <p className='text-xl mt-1'>{"(You may select more than 1 service)"}</p>
                        <Grid container spacing={2} className='!mt-2'>
                            <Grid item xs={12}>
                                <h3 className='text-2xl font-medium'>Luggage Delivery:</h3>
                                <FormControlLabel
                                    control={<Checkbox checked={services.luggageDeliveryArrival} onChange={handleCheckboxChange} name="luggageDeliveryArrival" />}
                                    label={`I only need luggage delivery on arrival at ${step1}`}
                                />
                                <FormControlLabel
                                    control={<Checkbox checked={services.luggageDeliveryDeparture} onChange={handleCheckboxChange} name="luggageDeliveryDeparture" />}
                                    label={`I only need luggage delivery on departure from ${step1}`}
                                />
                                <FormControlLabel
                                    control={<Checkbox checked={services.luggageDeliveryArrivalDeparture} onChange={handleCheckboxChange} name="luggageDeliveryArrivalDeparture" />}
                                    label={`I need luggage delivery on arrival and departure from ${step1}`}
                                />
                                <FormControlLabel
                                    control={<Checkbox checked={services.noLuggageDelivery} onChange={handleCheckboxChange} name="noLuggageDelivery" />}
                                    label="I don’t need luggage delivery service at this time"
                                />
                            </Grid>
                            <Grid item xs={12}>
                                <h3 className='text-2xl font-medium'>Luggage Storage:</h3>
                                <FormControlLabel
                                    control={<Checkbox checked={services.luggageStorageArrival} onChange={handleCheckboxChange} name="luggageStorageArrival" />}
                                    label={`I only need luggage storage on arrival at ${step1}`}
                                />
                                <FormControlLabel
                                    control={<Checkbox checked={services.luggageStorageDeparture} onChange={handleCheckboxChange} name="luggageStorageDeparture" />}
                                    label={`I only need luggage storage on departure from ${step1}`}
                                />
                                <FormControlLabel
                                    control={<Checkbox checked={services.luggageStorageArrivalDeparture} onChange={handleCheckboxChange} name="luggageStorageArrivalDeparture" />}
                                    label={`I need luggage storage on arrival and departure from ${step1}`}
                                />
                                <FormControlLabel
                                    control={<Checkbox checked={services.noLuggageStorage} onChange={handleCheckboxChange} name="noLuggageStorage" />}
                                    label="I don’t need luggage storage service at this time"
                                />
                            </Grid>
                            <Grid item xs={12}>
                                <h3 className='text-2xl font-medium'>Lost Luggage Retrieval:</h3>
                                <FormControlLabel
                                    control={<Checkbox checked={services.lostLuggageRetrieval} onChange={handleCheckboxChange} name="lostLuggageRetrieval" />}
                                    label={`I need lost luggage retrieval service from ${step1}`}
                                />
                                <FormControlLabel
                                    control={<Checkbox checked={services.noLostLuggageRetrieval} onChange={handleCheckboxChange} name="noLostLuggageRetrieval" />}
                                    label="I don’t need lost luggage retrieval service at this time"
                                />
                            </Grid>
                        </Grid>
                    </Grid>
                    <Grid item xs={12}>
                        {error && (
                            <Typography color="error" className='absolute bottom-16 left-14 w-full'>
                                {error}
                            </Typography>
                        )}
                        <div className='absolute -bottom-12 left-14 w-full flex justify-center pb-5'>
                            <button
                                className='bg-[#FFDE59] px-12 py-3 rounded-lg text-2xl font-semibold text-[#1F1F1F] hover:bg-opacity-85 transition-opacity duration-500 mr-16'
                                type='submit'
                            >
                                Submit
                            </button>
                        </div>
                    </Grid>
                </Grid>
            </form>
        </div>
    );
};

export default Step2;
