import React, { useState } from 'react';
import { Tabs, Tab, Box, Typography, Container } from '@mui/material';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import { pricingImg } from '../../../assets/Home';
const theme = createTheme({
    palette: {
        primary: {
            main: '#EB2131',
        },
        secondary: {
            main: '#FFDE59',
        },
    },
    typography: {
        h6: {
            fontWeight: 'bold',
        },
        body1: {
            fontSize: '1.1rem',
        },
    },
});

const Pricing = () => {
    const [tabIndex, setTabIndex] = useState(0);

    const handleTabChange = (_, newIndex) => {
        setTabIndex(newIndex);
    };

    return (
        <ThemeProvider theme={theme}>
            <Container className='!mt-28'>
                <Tabs value={tabIndex} onChange={handleTabChange} centered>
                    <Tab label="Delivery Price" />
                    <Tab label="Storage Price" />
                </Tabs>
                <Box hidden={tabIndex !== 0}>
                    <Box display="flex" flexDirection="column" alignItems="center" p={3} bgcolor="#FFDE59" color="#000" borderRadius={2} mt={2}>
                        <Typography variant="h6" gutterBottom>
                            Our Delivery Price:
                        </Typography>
                        <Typography variant="body1" align="center">
                            <strong>$34.99</strong> First 2 bag(s): Up to 30 Miles<br />
                            <strong>+ $10 Per Bag</strong> 3-8 bags<br />
                            <strong>+ $1 Per Mile</strong> 31-250 Miles
                        </Typography>
                    </Box>
                    <Box display="flex" justifyContent="center" p={3} mt={2} bgcolor="#FFFFFF" borderRadius={2} border="1px solid #FFDE59">
                        <img src={pricingImg} alt="Payment Methods" />
                    </Box>
                </Box>
                <Box hidden={tabIndex !== 1}>
                    <Box display="flex" flexDirection="column" alignItems="center" p={3} bgcolor="#FFDE59" color="#000" borderRadius={2} mt={2}>
                        <Typography variant="h6" gutterBottom>
                            Our Storage Price:
                        </Typography>
                        <Typography variant="body1" align="center">
                            <strong>$9.99</strong> Per Suitcase (24hrs Only)<br />
                            <strong>+ $5.99</strong> Per Carry On/Personal (24hrs Only)<br />
                            <strong>+ $2 OFF Daily Price</strong> Long Term Storage up to 30 days
                        </Typography>
                    </Box>
                    <Box display="flex" justifyContent="center" p={3} mt={2} bgcolor="#FFFFFF" borderRadius={2} border="1px solid #FFDE59">
                        <img src={pricingImg} alt="Payment Methods" />
                    </Box>
                </Box>
            </Container>
        </ThemeProvider>
    );
};

export default Pricing;
