import React, { useEffect, useState } from "react";
import { luggageBag } from "../../../assets/Departure";

const AnimatedBag = () => {
  const [isScrolled, setIsScrolled] = useState(false);
  useEffect(() => {
    const handleScroll = () => {
      const scrollTop = window.pageYOffset;
      setIsScrolled(scrollTop > 550);
    };

    window.addEventListener("scroll", handleScroll);

    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);
  return (
    <div className="flex justify-center my-36">
      <div className="w-[94.5vw]">
        <div className="flex justify-center">
          <img
            src={luggageBag}
            alt="luggageBag"
            className={`${
              !isScrolled ? "w-[91vw] -mt-52" : "w-[25vw] object-contain -mt-24"
            } transition-all duration-1000`}
            id="expand"
          />
        </div>
        <h1 className="text-4xl font-semibold text-center">
          Streamlined Airport Transfers
        </h1>
        <div className="flex justify-center">
          <p className="text-xl text-gray-500 max-w-[50%] text-center mt-8">
            Book hassle-free luggage pickup and transfer to the airport from
            your location with ease. Enjoy convenient scheduling and reliable
            service, ensuring your belongings arrive safely and on time for your
            departure.
          </p>
        </div>
      </div>
    </div>
  );
};

export default AnimatedBag;
