import React from "react";
import "./index.css";
import EmblaCarousel from "./EmblaCarousel/EmblaCarousel";
import {
  baggageClaim,
  american,
  delta,
  frontier,
  south,
  united,
  qatar
} from '../../../assets/Home';

function Hero() {
  const OPTIONS = { loop: true };
  const SLIDE_COUNT = 2;
  const SLIDES = Array.from(Array(SLIDE_COUNT).keys());

  return (
    <div className="relative">
      <EmblaCarousel slides={SLIDES} options={OPTIONS} />
      <div className="absolute top-0 left-0 h-screen beforeLg:w-[46%] w-fit flex flex-col justify-center">
        <div className="beforeLg:ml-20 ml-10">
          <img
            src={baggageClaim}
            alt="baggageClaim"
            className="w-[330px]  drop-shadow-2xl z-50 mt-10 -ml-2"
          />
          <p className="text-6xl leading-[60px] mb-9 -mt-2 text-with-stroke text-transparent !font-medium">
            Elevate Your Travel Experience with Baggage Claimed
          </p>
          <p className="text-black text-xl beforeLg:w-[70%] w-[85%]">
            VIP luggage storage and transfer service for skipping baggage claim hassles
          </p>
        </div>
        <div className="mt-10 absolute bottom-8 md:w-[98vw] w-screen flex justify-around items-center">

          <img src={american} alt="american" className="w-48" />

          <img src={delta} alt="delta" className="w-36" />

          <img src={frontier} alt="frontier" className="w-36" />

          <img src={south} alt="south" className="w-36" />

          <img src={united} alt="united" className="w-36" />

          <img src={qatar} alt="qatar" className="w-36" />

        </div>
      </div>
    </div>
  );
}

export default Hero;
