import React from 'react';
import logo from "../../assets/logo.png";
import { Link, useNavigate } from 'react-router-dom';
import { GoogleOAuthProvider, GoogleLogin } from '@react-oauth/google';
import { jwtDecode } from "jwt-decode";
import { google, apple, porter } from '../../assets/loginSignup';

const First = ({ setFirst, setStep1 }) => {
    const navigate = useNavigate()
    const handleGoogleSuccess = (response) => {
        const credential = response.credential;
        const decoded = jwtDecode(credential);

        const userInfo = {
            name: decoded.name,
            email: decoded.email,
            picture: decoded.picture,
        };

        navigate("/googlesuccess", { state: userInfo })
    };

    const handleGoogleFailure = (response) => {
        console.log("Google Login Failure:", response);
    };

    return (
        <GoogleOAuthProvider clientId="130246921083-m7ut9jbvr2a9t84rh5qgaluvt6bjkhpi.apps.googleusercontent.com">
            <div className='col-span-5 pt-1'>
                <Link to={"/"}>
                    <img src={logo} alt="logo" className="ml-[5vw] h-16 drop-shadow-sm z-50 mb-24" />
                </Link>
                <div className='flex items-center flex-col'>
                    <h1 className='text-center text-3xl font-bold w-[65%] text-[#453536] mb-10'>Ready to breeze through your travels? Join us now!</h1>
                    <button className='w-[67%] text-white bg-[#EB2131] rounded-2xl py-4 font-semibold text-lg mb-4' onClick={() => { setFirst(false); setStep1(true) }}>Signup with email</button>
                    <GoogleLogin
                        onSuccess={handleGoogleSuccess}
                        onError={handleGoogleFailure}
                        render={renderProps => (
                            <button className='w-[67%] rounded-2xl py-4 justify-center font-semibold text-lg mb-4 flex gap-2 items-center border-black border-[1px]' onClick={renderProps.onClick} disabled={renderProps.disabled}>
                                <img src={google} alt="google" className='h-5' /> Sign up with Google
                            </button>
                        )}
                    />
                    <button className='w-[67%] rounded-2xl py-4 justify-center font-semibold text-lg mb-8 flex gap-2 items-center border-black border-[1px]'>
                        <img src={apple} alt="apple" className='h-5' /> Sign up with Apple
                    </button>

                    <div className='text-center'>
                        <p className='text-gray-500'>Already have an account?</p>
                        <Link to="/login" className='text-[#EB2131]'>Log in instead</Link>
                    </div>
                </div>
            </div>
            <div className='col-span-5'>
                <img src={porter} alt='porter' className='h-screen !w-full object-cover rounded-lg' />
            </div>
        </GoogleOAuthProvider>
    );
}

export default First;
