import React, { useState } from 'react'
import { LockOutlined } from '@mui/icons-material';
import axios from 'axios';
import { Avatar, Box, Container, CssBaseline, TextField, Typography, CircularProgress, Grid } from '@mui/material';
import { Bounce, toast } from 'react-toastify';
import { useNavigate } from 'react-router-dom';

const Step2 = () => {
    const [loading, setLoading] = useState(false);
    const [otp, setOtp] = useState('');
    const navigate = useNavigate()

    const handleOtpChange = (e) => {
        setOtp(e.target.value);
    };

    const handleOtpSubmit = async (e) => {
        e.preventDefault();
        setLoading(true);

        const user = JSON.parse(localStorage.getItem('user'));
        const email = user.personal.email;

        try {
            const response = await axios.post('https://workbytech.host/luggage/api/register/verify/email/code', {
                email: email,
                verify_code: otp
            }, {
                headers: {
                    'Content-Type': 'application/json',
                    'X-CSRF-TOKEN': 'lAU5hI05NazejTBQ738cl8aAXCUygIhzbHG6jWla'
                }
            });

            console.log('Response Data:', response.data);
            if (response.data.status === 200) {
                localStorage.setItem('user', JSON.stringify(response.data.data.user));
                localStorage.setItem('token', response.data.data.token);
                toast.success('Account has been successfully verified.', {
                    position: "top-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                    transition: Bounce,
                })
                navigate(-1)
            }
        } catch (error) {
            console.error('There was an error verifying the account!', error);
        } finally {
            setLoading(false);
        }
    };
    return (
        <Container className="shadow-sm" component="main" maxWidth="xs">
            <CssBaseline />
            <Box
                sx={{
                    paddingTop: 15,
                    display: "flex",
                    flexDirection: "column",
                    alignItems: "center",
                }}
            >
                <Avatar sx={{ m: 1, bgcolor: "#EB2131" }}>
                    <LockOutlined />
                </Avatar>
                <Typography component="h1" variant="h5">
                    OTP Verification
                </Typography>
                <Box
                    component="form"
                    noValidate
                    onSubmit={handleOtpSubmit}
                    sx={{ mt: 3 }}
                >
                    <TextField
                        required
                        fullWidth
                        id="otp"
                        label="Enter OTP"
                        type="text"
                        name="otp"
                        autoComplete="one-time-code"
                        value={otp}
                        onChange={handleOtpChange}
                        sx={{ mb: 2 }}
                    />
                    {loading ? <div className='absolute top-0 left-0 h-[98vh] w-[98vw] flex justify-center items-center'><CircularProgress size={34} style={{ color: '#EB2131' }} /></div> : <button
                        type="submit"
                        className='!w-full rounded-xl py-4 bg-[#EB2131] text-white cursor-pointer mb-2'
                        disabled={loading}
                    >
                        Verify OTP
                    </button>}
                    <Grid container justifyContent="flex-end">
                        <Grid item>
                            <div>
                                {"Didn't"} receive an OTP?
                                <span
                                    className="text-[#2C3BFA] underline cursor-pointer"
                                >
                                    Resend OTP
                                </span>
                            </div>
                        </Grid>
                    </Grid>
                </Box>
            </Box>
        </Container>
    )
}

export default Step2