import React, { useState, useEffect } from 'react';
import { Link, useLocation, useNavigate } from 'react-router-dom';
import { Visibility, VisibilityOff } from '@mui/icons-material';
import { IconButton, TextField, CircularProgress } from '@mui/material';
import { Bounce, toast } from 'react-toastify';
import axios from 'axios';
import logo from '../../assets/logo.png';

const GoogleSuccess = () => {
    const location = useLocation();
    const [loading, setLoading] = useState(false);
    const navigate = useNavigate()
    const [passwordVisible, setPasswordVisible] = useState(false);
    const [formData, setFormData] = useState({
        first_name: '',
        last_name: '',
        email: '',
        phone: '',
        password: '',
        gender: '',
        dob: '',
        country_id: '',
    });

    useEffect(() => {
        if (location.state) {
            const [firstName, ...lastNameParts] = location.state.name.split(' ');
            const lastName = lastNameParts.join(' ');
            setFormData({
                ...formData,
                first_name: firstName,
                last_name: lastName,
                email: location.state.email,
            });
        }
    }, [location.state]);

    const togglePasswordVisibility = () => {
        setPasswordVisible(!passwordVisible);
    };

    const handleInputChange = (e) => {
        const { name, value } = e.target;
        setFormData({ ...formData, [name]: value });
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        setLoading(true);
        try {
            const response = await axios.post('https://workbytech.host/luggage/api/register', formData, {
                headers: {
                    'Content-Type': 'application/json',
                    'X-CSRF-TOKEN': 'lAU5hI05NazejTBQ738cl8aAXCUygIhzbHG6jWla'
                }
            });
            console.log('Response Data:', response.data);
            if (response.data.status === 200) {
                localStorage.setItem('user', JSON.stringify(response.data.data.user));
                toast.success('Account created successfully. Verification email sent!', {
                    position: "top-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                    transition: Bounce,
                });
                navigate("/googlesuccess/otpVerify")
            }
        } catch (error) {
            console.error('There was an error creating the account!', error);
        } finally {
            setLoading(false);
        }
    };

    const isFormComplete = () => Object.values(formData).every(field => {
        if (typeof field === 'string') {
            return field.trim() !== '';
        }
        return field !== null && field !== undefined;
    });

    return <>
        <Link to={"/"}>
            <img src={logo} alt="logo" className="ml-[5vw] mt-2 h-16 drop-shadow-sm z-50" />
        </Link >
        <form className="space-y-4 w-[35%] mx-auto" onSubmit={handleSubmit}>
            <h1 className='text-3xl font-semibold mb-10'>Add additional information</h1>
            <div className="grid grid-cols-2 gap-4">
                <TextField
                    id="filled-basic"
                    label="First name"
                    variant="filled"
                    className="w-full !rounded-lg"
                    name="first_name"
                    value={formData.first_name}
                    onChange={handleInputChange}
                />
                <TextField
                    id="filled-basic"
                    label="Last name"
                    variant="filled"
                    className="w-full !rounded-lg"
                    name="last_name"
                    value={formData.last_name}
                    onChange={handleInputChange}
                />
            </div>
            <TextField
                id="filled-basic"
                label="Email"
                variant="filled"
                className="w-full !rounded-lg"
                name="email"
                value={formData.email}
                onChange={handleInputChange}
                disabled
            />
            <TextField
                id="filled-basic"
                label="Phone number"
                variant="filled"
                className="w-full !rounded-lg"
                name="phone"
                value={formData.phone}
                onChange={handleInputChange}
            />
            <div className="relative">
                <TextField
                    id="filled-basic"
                    label="Create password"
                    variant="filled"
                    type={passwordVisible ? "text" : "password"}
                    className="w-full !rounded-lg"
                    name="password"
                    value={formData.password}
                    onChange={handleInputChange}
                />
                <IconButton
                    className="!absolute !right-3 !top-2"
                    onClick={togglePasswordVisibility}
                >
                    {passwordVisible ? <Visibility /> : <VisibilityOff />}
                </IconButton>
            </div>
            <TextField
                id="filled-basic"
                label="Gender"
                variant="filled"
                className="w-full !rounded-lg"
                name="gender"
                value={formData.gender}
                onChange={handleInputChange}
            />
            <TextField
                id="filled-basic"
                label="Date of Birth"
                variant="filled"
                type="date"
                className="w-full !rounded-lg"
                name="dob"
                value={formData.dob}
                onChange={handleInputChange}
                InputLabelProps={{
                    shrink: true,
                }}
            />
            <TextField
                id="filled-basic"
                label="Country ID"
                variant="filled"
                className="w-full !rounded-lg"
                name="country_id"
                value={formData.country_id}
                onChange={handleInputChange}
            />
            <div className="w-full flex justify-center">
                {loading ? <div className='absolute top-0 left-0 h-[98vh] w-[98vw] flex justify-center items-center'><CircularProgress size={34} style={{ color: '#EB2131' }} /></div> : <button type="submit" className={`w-full rounded-xl py-4 ${isFormComplete() ? 'bg-[#EB2131] text-white cursor-pointer' : 'bg-[#e1e5eb] text-[#b0b8c2] cursor-not-allowed'}`}
                    disabled={!isFormComplete() || loading}>
                    Continue
                </button>}
            </div>
        </form>
    </>;
}

export default GoogleSuccess;
