import React, { useEffect, useState } from "react";
import { coolie, stressFree, effortlessTravel } from "../../../assets/Departure";

const Departure2 = () => {
  const [isScrolled, setIsScrolled] = useState(false);
  const [come, setCome] = useState(false);

  useEffect(() => {
    let isMounted = true;

    const handleScroll = () => {
      if (isMounted) {
        const scrollTop = window.pageYOffset;
        setIsScrolled(scrollTop >= 1200);
      }
    };

    const debouncedScroll = debounce(handleScroll, 50); // Lower debounce delay

    function debounce(func, delay) {
      let timer;
      return function () {
        const context = this;
        const args = arguments;
        clearTimeout(timer);
        timer = setTimeout(() => {
          func.apply(context, args);
        }, delay);
      };
    }

    window.addEventListener("scroll", debouncedScroll);

    return () => {
      isMounted = false;
      window.removeEventListener("scroll", debouncedScroll);
    };
  }, []);

  useEffect(() => {
    let timer;
    if (isScrolled) {
      timer = setTimeout(() => {
        setCome(true);
      }, 0);
    } else {
      setCome(false);
    }

    return () => clearTimeout(timer); // Clean up the timer on unmount or if isScrolled changes
  }, [isScrolled]);

  return (
    <>
      <div
        className={`flex justify-center py-20 mt-10 bg-[#FFFF00] ${come ? "min-h-[80vh]" : "min-h-screen"
          }`}
      >
        <div className="w-[94vw] relative">
          <div className="text-center">
            <h1 className="text-3xl font-bold">
              Ready to Travel Light with Stress-Free Departures?
            </h1>
            <p className="mt-3 text-gray-600 text-xl">
              Effortlessly schedule pickup and delivery of your luggage for a
              seamless travel experience.
            </p>
          </div>
          <img
            src={stressFree}
            alt=""
            className={`${!isScrolled ? "w-full h-full" : "h-[20vw] w-[20vw] rounded-full"
              } duration-1000 transition-all mx-auto mt-10 object-cover`}
          />
          <div
            className={`${come ? "-opacity-100 flex justify-center" : "opacity-0"
              } transition-all duration-1000 -mt-[280px]`}
          >
            <div className="grid gap-16 grid-cols-3">
              <div className="flex flex-col items-center gap-7">
                <img
                  className="w-[20vw] h-[20vw] object-cover rounded-full"
                  src={coolie}
                  alt=""
                />
                <h1 className="text-3xl font-bold text-center">
                  Departures Made Easy
                </h1>
                <p className="text-gray-600 text-lg text-center px-4">
                  Booking luggage pickup and transfer to the airport is a
                  breeze, giving you more time to relax and enjoy your travel
                  experience.
                </p>
              </div>
              <div className="flex flex-col items-center gap-7">
                <h1 className="text-3xl font-bold text-center mt-[calc(20vw+28px)]">
                  Stress-Free Departures
                </h1>
                <p className="text-gray-600 text-lg text-center px-4">
                  {"You'll"} save 36+ hours a year {"-"} enough time for a
                  relaxing weekend getaway by effortlessly booking luggage
                  pickup and airport transfers.
                </p>
              </div>
              <div className="flex flex-col items-center gap-7">
                <img
                  className="w-[20vw] h-[20vw] object-cover rounded-full"
                  src={effortlessTravel}
                  alt=""
                />
                <h1 className="text-3xl font-bold text-center">
                  Effortless Travel Planning
                </h1>
                <p className="text-gray-600 text-lg text-center px-4">
                  Easily arrange for luggage pickup and airport transfer,
                  allowing you to focus on the excitement of your upcoming trip.
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Departure2;
