import React, { useState } from 'react';
import {
    TextField,
    Select,
    MenuItem,
    FormControl,
    InputLabel,
    InputAdornment,
    IconButton,
    Typography,
    ThemeProvider,
    createTheme,
    CircularProgress,
} from "@mui/material";
import { ArrowForward } from '@mui/icons-material';
import axios from 'axios';
import { Bounce, toast } from 'react-toastify';

const theme = createTheme({
    components: {
        MuiOutlinedInput: {
            styleOverrides: {
                root: {
                    borderRadius: 0,
                },
            },
        },
        MuiInputLabel: {
            styleOverrides: {
                root: {
                    backgroundColor: '#fff',
                    paddingRight: '4px',
                },
            },
        },
        MuiSelect: {
            styleOverrides: {
                root: {
                    borderRadius: 0,
                },
            },
        },
    },
});

const airlines = [
    'American Airlines',
    'British Airways',
    'Delta Airlines',
    'Emirates',
    'Frontier Airlines',
    'KLM',
    'Other Airline',
];

const Step4 = ({ step2, step3, setShowThankYou }) => {
    const [airline, setAirline] = useState('');
    const [confirmationNumber, setConfirmationNumber] = useState('');
    const [flightNumber, setFlightNumber] = useState('');
    const [loading, setLoading] = useState(false);
    function generateRandomNumber(length) {
        let result = '';
        const characters = '0123456789';

        for (let i = 0; i < length; i++) {
            const randomIndex = Math.floor(Math.random() * characters.length);
            result += characters.charAt(randomIndex);
        }

        return result;
    }

    const handleSubmit = async () => {
        setLoading(true);
        const generateItemsArray = () => {
            const items = [];
            const addItem = (count, comment) => {
                for (let i = 0; i < count; i++) {
                    items.push({
                        Comments: comment,
                        Description: "",
                        TrackingNumber: generateRandomNumber(7),
                    });
                }
            };
            addItem(step3.checkedInCount, "Checked-in item");
            addItem(step3.carryOnCount, "Carry-on item");
            addItem(step3.oversizedCount, "Oversized item");
            return items;
        };

        const data = {
            Customer: step2?.ID,
            Items: generateItemsArray(),
            CollectionLocation: {
                Zone: step3.pickUpLocationName !== "Home" ? null : step3.pickUpLocation?.Zone,
                AddressLine1: step3.pickUpLocationName !== "Home" ?
                    step3?.pickUpLocation : step3.pickUpLocation.AddressLine1,
                City: step3.pickUpLocationName !== "Home" ? step3.pickUpCity : step3.pickUpLocation?.City,
                State: step3.pickUpLocationName !== "Home" ? step3.pickUpState : step3.pickUpLocation?.State,
                PostalCode: step3.pickUpLocationName !== "Home" ? step3.pickUpZipCode : step3.pickUpLocation?.PostalCode,
                Country: 'United States of America',
                Email: step2.Email,
                Phone: step2.Phone,
            },
            DeliveryLocation: {
                Zone: step3.deliveryLocation !== "Home" ? null : step3.deliveryDetails?.Zone,
                AddressLine1: step3.deliveryLocation !== "Home" ? step3.deliveryDetails : step3.deliveryDetails?.AddressLine1,
                City: step3.deliveryLocation !== "Home" ? step3.deliveryCity : step3.deliveryDetails.City,
                State: step3.deliveryLocation !== "Home" ? step3.deliveryState : step3.deliveryDetails.State,
                PostalCode: step3.deliveryLocation !== "Home" ? step3.deliveryZipCode : step3.deliveryDetails.PostalCode,
                Country: 'United States of America',
                Email: step2.Email,
                Phone: step2.Phone,
            },
            PriceModifiers: [
                { Name: 'Sample Tax', Price: 19.95 },
                { Name: 'Sample Tax', Price: 19.95 }
            ],
            TaxTotal: 19.95,
            Subtotal: 21.8,
            TrackingNumber: generateRandomNumber(7),
            CODAmount: 50.0,
            BasePriceType: 2,
            PriceAdjustment: 0.0,
            PriceModifierTotalCost: 22.95,
            Description: 'This is an example.',
            Comments: 'This is an example.',
            RouteName: 'Sample Route',
            StatusLevel: 2,
            CollectionArrivalDate: `${step3.collectionDate}T${step3.collectionTime}:00`,
            CollectionContactName: step2.Name,
            CollectionCODRequired: false,
            CollectionSignatureRequired: false,
            DeliveryArrivalDate: `${step3.deliveryDate}T${step3.deliveryTime}:00`,
            DeliveryContactName: step2.Name,
            DeliveryCODRequired: true,
            DeliverySignatureRequired: true
        };
        console.log(step2);
        try {
            const response = await axios.post('https://secure.ontime360.com/sites/aafcouriers/api/order/post', data, {
                headers: {
                    'Content-Type': 'application/json',
                    'Accept': 'application/json',
                    'Authorization': '4fe1b4ce-9dd4-4375-abb8-31bab61bd149'
                }
            });
            console.log(response.data);
            const res = await axios.post("https://workbytech.host/luggage/api/records", {
                reference_number: response.data?.ID,
                extra: {
                    orderId: response?.data?.TrackingNumber,
                    name: response?.data?.CollectionContactName,
                    items: response?.data?.Items,
                    services: step2?.services,
                    Customer: step2?.ID,
                }
            })
            toast.success('Order Booked Succefully!', {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "light",
                transition: Bounce,
            });
            setAirline('');
            setConfirmationNumber('');
            setFlightNumber('');
            console.log(res?.data);
        } catch (error) {
            console.error('There was an error!', error);
        } finally {
            setLoading(false);
        }
    };

    return <>
        {/* <h1 className="text-4xl !font-extrabold text-[#1F1F1F]">Enter Luggage Details</h1>
            {details.map((detail, index) => (
                <div key={index} className='mt-7'>
                    <Grid container spacing={2}>
                        {index == 0 && (
                            <Grid item xs={12} className='flex justify-end'>
                                <IconButton onClick={() => removeDetail(index)} disabled={details.length === 1}>
                                    <Remove />
                                </IconButton>
                                <IconButton onClick={addDetail}>
                                    <Add />
                                </IconButton>
                            </Grid>
                        )}
                        <Grid item xs={12}>
                            <TextField
                                fullWidth
                                label="Description"
                                name="Description"
                                value={detail.Description}
                                onChange={(e) => handleInputChange(index, e)}
                                className='!rounded-2xl'
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <TextField
                                fullWidth
                                label="Comments"
                                name="Comments"
                                value={detail.Comments}
                                onChange={(e) => handleInputChange(index, e)}
                                className='!rounded-2xl'
                            />
                        </Grid>
                        <Grid item xs={3}>
                            <TextField
                                fullWidth
                                label="Weight"
                                name="Weight"
                                type="number"
                                value={detail.Weight}
                                onChange={(e) => handleInputChange(index, e)}
                                className='!rounded-2xl'
                            />
                        </Grid>
                        <Grid item xs={3}>
                            <TextField
                                fullWidth
                                label="Height"
                                name="Height"
                                type="number"
                                value={detail.Height}
                                onChange={(e) => handleInputChange(index, e)}
                                className='!rounded-2xl'
                            />
                        </Grid>
                        <Grid item xs={3}>
                            <TextField
                                fullWidth
                                label="Width"
                                name="Width"
                                type="number"
                                value={detail.Width}
                                onChange={(e) => handleInputChange(index, e)}
                                className='!rounded-2xl'
                            />
                        </Grid>
                        <Grid item xs={3}>
                            <TextField
                                fullWidth
                                label="Length"
                                name="Length"
                                type="number"
                                value={detail.Length}
                                onChange={(e) => handleInputChange(index, e)}
                                className='!rounded-2xl'
                            />
                        </Grid>

                    </Grid>
                    <Divider className='!mt-4' />
                </div>
            ))}
            <button
                className='bg-[#FFDE59] w-full py-5 rounded-3xl text-2xl font-semibold text-[#1F1F1F] hover:bg-opacity-85 transition-opacity duration-500 mt-7'
                onClick={handleSubmit}
            >
                Submit
            </button> */}
        <ThemeProvider theme={theme}>
            <section className="flex flex-col items-center">
                <Typography variant="h4" className="font-bold">Payment</Typography>
                <Typography variant="subtitle1" className="mb-16">Confirm your flight details and enter your payment information</Typography>
                <div className="w-full">
                    <FormControl variant="outlined" fullWidth margin="normal" className='!-mb-[2px]'>
                        <InputLabel id="airline-select-label">Which airline are you flying with?</InputLabel>
                        <Select
                            labelId="airline-select-label"
                            id="airline-select"
                            value={airline}
                            onChange={(e) => setAirline(e.target.value)}
                            label="Which airline are you flying with?"
                        >
                            {airlines.map((airline) => (
                                <MenuItem value={airline} key={airline}>
                                    {airline}
                                </MenuItem>
                            ))}
                        </Select>
                    </FormControl>
                    <TextField
                        fullWidth
                        label="What is your flight Confirmation Number?"
                        type="text"
                        value={confirmationNumber}
                        onChange={(e) => setConfirmationNumber(e.target.value)}
                        className="!mt-6"
                        InputProps={{
                            endAdornment: (
                                <InputAdornment position="end">
                                    <IconButton>
                                        <ArrowForward />
                                    </IconButton>
                                </InputAdornment>
                            ),
                        }}
                    />
                    <TextField
                        fullWidth
                        label="What is your Airline Flight Number?"
                        type="text"
                        value={flightNumber}
                        onChange={(e) => setFlightNumber(e.target.value)}
                        className="!mt-6"
                        InputProps={{
                            endAdornment: (
                                <InputAdornment position="end">
                                    <IconButton>
                                        <ArrowForward />
                                    </IconButton>
                                </InputAdornment>
                            ),
                        }}
                    />
                </div>
                <div className='flex justify-center mt-8 mb-3'>

                    {loading ? (
                        <div className='absolute top-0 left-0 h-[98vh] w-[95vw] flex justify-center items-center'>
                            <CircularProgress size={34} style={{ color: '#EB2131' }} />
                        </div>
                    ) : <button
                        className='bg-[#FFDE59] px-12 py-3 rounded-lg text-2xl font-semibold text-[#1F1F1F] hover:bg-opacity-85 transition-opacity duration-500'
                        onClick={handleSubmit}
                    >
                        Submit
                    </button>}
                </div>
            </section>

        </ThemeProvider>
    </>
}

export default Step4
