import React from "react";
import { Footer, Header } from '../../Component';

const CookiePolicy = () => <>
    <Header />
    <section className="flex flex-col items-center mt-28 mb-20 font-serif">
        <div className="w-[98%] rounded-3xl flex flex-col items-center bg-[#FFDE59] py-20">
            <div>
                <h1 className="text-[#1F1F1F] text-7xl font-extrabold">
                    Cookie policy
                </h1>
                <p className="mt-1 !font-thin">
                    Last updated<span className="font-bold">17th Nov 2023</span>
                </p>
            </div>
        </div>
        <div className="w-[56vw] mt-14">
            <div>
                <h1 className="text-3xl font-semibold text-[#1F1F1F]">
                    Introduction
                </h1>
                <div className="mt-4 text-[#1F1F1F] font-thin">
                    <p>
                        Baggage Claimed Fibre Limited {"(“Baggage Claimed”, “we, “our”)"}{" "}
                        uses cookies on its website. This Cookie Notice explains more
                        about cookies and why we use them. It also explains how you can
                        control and opt out of receiving them.
                    </p>
                    <p className="mt-4">
                        This notice should be read together with our Privacy Notice which
                        sets out how and why we collect, store, use and share personal
                        information generally, as well as your rights in relation to your
                        personal information and details of how to contact us and
                        supervisory authorities if you have a complaint.
                    </p>
                    <p className="mt-4">
                        We update this Cookie Notice from time to time, in response to
                        changes in applicable laws and regulations, changes to our
                        processing practices and to products and services we offer. When
                        changes are made, we will update the final section of this notice.
                        Please review this Cookie Notice periodically to check for
                        updates.
                    </p>
                </div>
            </div>
            <div className="mt-5">
                <h1 className="text-3xl font-semibold text-[#1F1F1F]">
                    What are cookies?
                </h1>
                <div className="mt-4 text-[#1F1F1F] font-thin">
                    <p>
                        Cookies are small text files that are stored on your browser or
                        device by websites, apps, online media and advertisements. There
                        are different types of cookies. Cookies used by the company that
                        operates the website you are visiting are called “first party
                        cookies”. Therefore, cookies when visiting our website are first
                        party cookies.
                    </p>
                    <p className="mt-4">
                        Cookies served by companies that are not operating the domain you
                        are visiting are called “third party cookies”. For example, we may
                        allow Google to set a cookie on your browser while you visit our
                        website https://www.Baggage Claimed.co/broadband and that would be a
                        third-party cookie.
                    </p>
                    <p className="mt-4">
                        Cookies may also last for different periods of time. “Session
                        cookies” only last as long as your browser is open. These are
                        deleted automatically once you close your browser. Other cookies
                        are “persistent cookies”, meaning that they survive after your
                        browser is closed. For example, they may recognise your device
                        when you re-open your browser and browse the internet again. When
                        you visit the Baggage Claimed websites, we will ask you whether you agree
                        to our use of cookies.
                    </p>
                </div>
            </div>
            <div className="mt-5">
                <h1 className="text-3xl font-semibold text-[#1F1F1F]">
                    How do we use cookies?
                </h1>
                <p className="mt-4 text-[#1F1F1F] font-thin">
                    We use cookies to distinguish you from other users. We also use them
                    for analytics and advertising so we can improve your experience and
                    the relevance of what you see when you access our website.
                </p>
            </div>
            <div className="mt-5">
                <h1 className="text-3xl font-semibold text-[#1F1F1F]">
                    Do I have any options about use of cookies?
                </h1>
                <p className="mt-4 text-[#1F1F1F] font-thin">
                    As a user of our website, you can choose what cookies to accept.
                    Simply browsing our website does not mean you agree to the use of
                    cookies. We ask you when you first visit our website via a pop-up
                    banner to accept or reject the use of cookies. This pop-up clearly
                    explains why we use them and asks you to make an affirmative action
                    by clicking {"“Continue”"}.
                </p>
            </div>
            <div className="mt-5">
                <h1 className="text-3xl font-semibold text-[#1F1F1F]">
                    What Cookies do we use?
                </h1>
                <p className="mt-4 text-[#1F1F1F] font-thin">
                    The cookies we use on our website fall into the following
                    categories:
                </p>
            </div>
            <div className="mt-5">
                <h1 className="text-3xl font-semibold text-[#1F1F1F]">
                    Essential Cookies
                </h1>
                <div className="mt-4 text-[#1F1F1F] font-thin">
                    <p>
                        These cookies are essential for the proper functioning and
                        security of our website. They enable users to log in to their
                        accounts and sign up for our services. Essential cookies retain
                        important information such as shopping cart items and form
                        submissions. Additionally, they contribute to security measures
                        like authentication and fraud prevention, safeguarding user data
                        and transactions.
                    </p>
                </div>
            </div>
            <div className="mt-5">
                <h1 className="text-3xl font-semibold text-[#1F1F1F]">
                    Analytics and Customisation Cookies
                </h1>
                <div className="mt-4 text-[#1F1F1F] font-thin">
                    <p>
                        We use these Cookies to help us understand our customers and spot
                        ways in which we can improve our service to you. We may customise
                        your experience to give you more relevant information. These
                        cookies play a vital role in enhancing user experience and
                        optimising website performance. By enabling personalisation
                        features, such as storing user preferences and facilitating
                        website optimisation tests, they ensure a tailored and seamless
                        browsing experience. Additionally, these cookies support website
                        analytics, collecting data on visit duration, user behaviour, and
                        browsing patterns. This data helps us analyse website traffic,
                        visitor demographics, and user engagement, allowing us to better
                        understand our customers and identify areas for improvement in our
                        services.
                    </p>
                </div>
            </div>
            <div className="mt-5">
                <h1 className="text-3xl font-semibold text-[#1F1F1F]">
                    Marketing and Social Media Cookies
                </h1>
                <div className="mt-4 text-[#1F1F1F] font-thin">
                    <p>
                        We use these to give you more relevant offers and to measure the
                        effectiveness of our advertising campaigns. These cookies are
                        utilized for advertising purposes, allowing for the storage of
                        data such as ad performance metrics and user interactions. They
                        enable the measurement of advertising campaign effectiveness and
                        the customization of ads to be more relevant to individual users.
                        Additionally, advertising cookies support retargeting efforts by
                        sharing information about visited pages with advertising platforms
                        to deliver personalised content based on user interests and
                        behaviour.
                    </p>
                </div>
            </div>
            <div className="mt-5">
                <h1 className="text-3xl font-semibold text-[#1F1F1F]">
                    3rd Party services used at Baggage Claimed
                </h1>
                <ul className="mt-4 text-[#1F1F1F] font-thin">
                    <li>
                        • Facebook pixel - Please see About Meta Pixel for more
                        information.
                    </li>
                    <li className="mt-3">
                        • Google ads - Please see Google Cookies for more information.
                    </li>
                    <li className="mt-3">
                        • Google Analytics - Please see Google Policies for more
                        information.
                    </li>
                    <li className="mt-3">
                        • Intercom - Enables us to talk with you via our online chat support.
                    </li>
                </ul>
            </div>
            <div className="mt-5">
                <h1 className="text-3xl font-semibold text-[#1F1F1F]">
                    What about cookies from other sites
                </h1>
                <p className="mt-10 text-[#1F1F1F] font-thin">
                    We may have links to other {"organisations’"} websites, apps or social plug-ins. When we include these links and social plugins, it gives those sites the flexibility to use cookies. They {"can’t"} read any cookies we set from our website, and we {"can’t"} read any cookies they set, but it lets them do the same kind of traffic measuring that we do on the rest of our website, and it also lets them know whether {"you’re"} logged in. Other websites and services (including, for example, advertising networks, providers of external services like web traffic analysis services and content recommendation engines) may also use cookies, which we cannot control. These cookies are likely to be analytical/performance cookies or targeting cookies. You need to make sure you are happy with those cookies before entering those websites.
                </p>
            </div>
            <div className="mt-10">
                <h1 className="text-3xl font-semibold text-[#1F1F1F]">
                    Changing Your Cookies Preferences
                </h1>
                <div className="mt-4 text-[#1F1F1F] font-thin">
                    <p>
                        You can allow or block cookies by activating the setting on your browser that permits you to change the setting of all or some cookies. If you would like more information on how to manage cookies on some popular browsers, please click on the links below.
                    </p>
                    <ul className="mt-4 flex flex-col gap-4 text-purple-600">
                        {["• Internet Explorer", "•  Google Chrome", "•  Apple Safari", "•  Microsoft Edge", "•  Mozilla Firefox"].map(li => (
                            <li className="hover:underline" key={li}>{li}</li>
                        ))}
                    </ul>
                    <p className="mt-12">
                        You can find more detailed information about how you can manage cookies at the https://allaboutcookies.org/ and   https://www.youronlinechoices.com/uk. Please note that if you do set your browser to reject cookies, you may not be able to use the features of the Baggage Claimed website. Most of the website will function normally, however, functions that rely on cookies such as the use of our online enquiry form, will be disabled.
                    </p>
                </div>
            </div>
            <div className="mt-12">
                <h1 className="text-3xl font-semibold text-[#1F1F1F]">
                    Contact Us
                </h1>
                <p className="mt-4 text-[#1F1F1F] font-thin">
                    If you have any questions about this Cookie Notice or the information we hold about you, please contact us by using the details set out in our Privacy Notice.
                </p>
            </div>
        </div>
    </section>
    <Footer />
</>

export default CookiePolicy;
