import React from 'react'
import {
    FormControl,
    InputLabel,
    MenuItem,
    Select,
} from "@mui/material";

const Step1 = ({ setShowUser, setStep1 }) => {
    const handleSubmit = e => {
        e.preventDefault()
        setShowUser(true)
    }
    return <>
        <h1 className="text-4xl !font-extrabold text-[#1F1F1F]">
            Available Airports for service
        </h1>
        <form onSubmit={handleSubmit}>
            <FormControl variant="outlined" fullWidth margin="normal" className='!mt-10'>
                <InputLabel id="airport-select-label">Select airport</InputLabel>
                <Select
                    labelId="airport-select-label"
                    id="airport-select"
                    onChange={(e) => {
                        const airportCode = e.target.value.split(' ').pop().replace('(', '').replace(')', '');
                        setStep1(airportCode);
                    }}
                    label="Select airport"
                >
                    {["Orlando International Airport (MCO)", "Kissimmee Gateway Airport (ISM", "Orlando Sanford International Airport (SFB)", "Leesburg International Airport (LEE)", "Orlando Melbourne International Airport (MLB)", "Tampa International Airport (TPA)", "Peter O. Knight Airport(TPF)", "St. Pete–Clearwater International Airport (PIE)", "Albert Airport (SPG)", "Sarasota Bradenton International Airport (SRQ)"].map((location, i) => (
                        <MenuItem value={location} key={i}>{location}</MenuItem>
                    ))}
                </Select>
            </FormControl>
            <button
                className="bg-[#FFDE59] w-full py-5 rounded-3xl text-2xl font-semibold text-[#1F1F1F] hover:bg-opacity-85 transition-opacity duration-500 mt-7"
                type='submit'
            >
                Submit
            </button>
        </form>
    </>
}

export default Step1
