import React, { useState } from 'react';
import {
    TextField,
    Select,
    MenuItem,
    FormControl,
    InputLabel,
    IconButton,
} from "@mui/material";
import { Add, Remove } from '@mui/icons-material';
import { checkedIn, carryon, oversized, airport, home, resort, office } from "../../assets/bookNow"

const Step3 = ({ setStep3, locationList, setShowItem }) => {
    const [pickUpLocation, setPickUpLocation] = useState('');
    const [pickUpLocationName, setPickUpLocationName] = useState('');
    const [pickUpCity, setPickUpCity] = useState('');
    const [pickUpState, setPickUpState] = useState('');
    const [pickUpZipCode, setPickUpZipCode] = useState('');
    const [deliveryCity, setDeliveryCity] = useState('');
    const [deliveryState, setDeliveryState] = useState('');
    const [deliveryZipCode, setDeliveryZipCode] = useState('');
    const [deliveryLocation, setDeliveryLocation] = useState('');
    const [deliveryDate, setDeliveryDate] = useState('');
    const [deliveryTime, setDeliveryTime] = useState('');
    const [collectionDate, setCollectionDate] = useState('');
    const [collectionTime, setCollectionTime] = useState('');
    const [returnDate, setReturnDate] = useState('');
    const [returnTime, setReturnTime] = useState('');
    const [deliveryDetails, setDeliveryDetails] = useState(null);
    const [checkedInCount, setCheckedInCount] = useState(0);
    const [carryOnCount, setCarryOnCount] = useState(0);
    const [oversizedCount, setOversizedCount] = useState(0);

    const handleSubmitDetails = async () => {
        setStep3({
            deliveryDetails,
            deliveryDate,
            deliveryTime,
            returnDate,
            returnTime,
            checkedInCount,
            carryOnCount,
            oversizedCount,
            collectionDate,
            collectionTime,
            pickUpLocationName,
            pickUpLocation,
            pickUpCity,
            pickUpState,
            pickUpZipCode,
            deliveryCity,
            deliveryState,
            deliveryZipCode,
            deliveryLocation,
        });
        setShowItem(true);
    };

    return (
        <section>
            <h1 className="text-4xl !font-extrabold text-[#1F1F1F] text-center">Get Your Luggage Delivered</h1>
            <p className='text-center text-lg mt-px'>By selecting your options below</p>
            <div className='absolute left-0'>
                <div className='w-[70vw] mx-[14vw] mt-10'>
                    <div className='grid grid-cols-2 gap-20'>
                        <div className='flex flex-col items-center'>
                            <h3 className='text-3xl font-medium text-center'>Pick-up From</h3>
                            <Info onSelect={setPickUpLocationName} selected={pickUpLocationName} />
                            {pickUpLocationName === "Airport" ? <FormControl variant="outlined" fullWidth margin="normal" className='!mt-10'>
                                <InputLabel id="airport-select-label">Select airport</InputLabel>
                                <Select
                                    labelId="airport-select-label"
                                    id="airport-select"
                                    value={pickUpLocation}
                                    onChange={(e) => setPickUpLocation(e.target.value)}
                                    label="Select airport"
                                >
                                    {["Orlando International Airport (MCO)", "Kissimmee Gateway Airport (ISM", "Orlando Sanford International Airport (SFB)", "Leesburg International Airport (LEE)", "Orlando Melbourne International Airport (MLB)", "Tampa International Airport (TPA)", "Peter O. Knight Airport(TPF)", "St. Pete–Clearwater International Airport (PIE)", "Albert Airport (SPG)", "Sarasota Bradenton International Airport (SRQ)"].map((location, i) => (
                                        <MenuItem value={location} key={i}>{location}</MenuItem>
                                    ))}
                                </Select>
                            </FormControl>
                                : pickUpLocationName === "Home" ? <FormControl variant="outlined" fullWidth margin="normal" className='!mt-10'>
                                    <InputLabel id="address-label" className='!bg-white !pr-1'>Select pickup address</InputLabel>
                                    <Select
                                        labelId="home-select-label"
                                        id="home-select"
                                        value={pickUpLocation}
                                        onChange={(e) => setPickUpLocation(e.target.value)}
                                        label="Select pickup address"
                                    >
                                        {locationList.map((location, i) => (
                                            <MenuItem value={location} key={i}>{location?.AddressLine1}</MenuItem>
                                        ))}
                                    </Select>
                                </FormControl> : <>
                                    <TextField
                                        fullWidth
                                        label={`Add Pick-up address`}
                                        type="text"
                                        value={pickUpLocation || ''}
                                        onChange={(e) => setPickUpLocation(e.target.value)}
                                        className='!mt-10'
                                    />
                                    <div className='grid grid-cols-3 gap-2 mt-2'>
                                        <TextField
                                            fullWidth
                                            label={`City`}
                                            type="text"
                                            value={pickUpCity || ''}
                                            onChange={(e) => setPickUpCity(e.target.value)}
                                        />

                                        <TextField
                                            fullWidth
                                            label={`State`}
                                            type="text"
                                            value={pickUpState || ''}
                                            onChange={(e) => setPickUpState(e.target.value)}
                                        />

                                        <TextField
                                            fullWidth
                                            label={`Zip Code`}
                                            type="text"
                                            value={pickUpZipCode || ''}
                                            onChange={(e) => setPickUpZipCode(e.target.value)}
                                        />
                                    </div>
                                    <div className='grid grid-cols-2 gap-2 mt-3 w-full'>
                                        <TextField
                                            fullWidth
                                            label="Pick-up Date"
                                            focused
                                            type="date"
                                            InputProps={{ style: { color: "#6b7280" } }}
                                            value={collectionDate || ''}
                                            onChange={(e) => setCollectionDate(e.target.value)}
                                        />

                                        <TextField
                                            fullWidth
                                            label="Pick-up Time"
                                            focused
                                            type="time"
                                            InputProps={{ style: { color: "#6b7280" } }}
                                            value={collectionTime || ''}
                                            onChange={(e) => setCollectionTime(e.target.value)}
                                        />
                                    </div>
                                </>
                            }

                        </div>
                        <div className='flex flex-col items-center'>
                            <h3 className='text-3xl font-medium text-center'>Delivered To</h3>
                            <Info onSelect={setDeliveryLocation} selected={deliveryLocation} />
                            {deliveryLocation === "Home" ? <FormControl variant="outlined" fullWidth margin="normal" className='!mt-10'>
                                <InputLabel id="address-label" className='!bg-white !pr-1'>Select your delivery address</InputLabel>
                                <Select
                                    labelId="address-label"
                                    id="address-select"
                                    value={deliveryDetails}
                                    onChange={(e) => setDeliveryDetails(e.target.value)}
                                    label="Select your address"
                                >
                                    {locationList.map((location, i) => (
                                        <MenuItem value={location} key={i}>{location?.AddressLine1}</MenuItem>
                                    ))}
                                </Select>
                            </FormControl> : deliveryLocation === "Airport" ? <FormControl variant="outlined" fullWidth margin="normal" className='!mt-10'>
                                <InputLabel id="airport-select-label">Select airport</InputLabel>
                                <Select
                                    labelId="airport-select-label"
                                    id="airport-select"
                                    value={deliveryDetails}
                                    onChange={(e) => setDeliveryDetails(e.target.value)}
                                    label="Select airport"
                                >
                                    {["Orlando International Airport (MCO)", "Kissimmee Gateway Airport (ISM", "Orlando Sanford International Airport (SFB)", "Leesburg International Airport (LEE)", "Orlando Melbourne International Airport (MLB)", "Tampa International Airport (TPA)", "Peter O. Knight Airport(TPF)", "St. Pete–Clearwater International Airport (PIE)", "Albert Airport (SPG)", "Sarasota Bradenton International Airport (SRQ)"].map((location, i) => (
                                        <MenuItem value={location} key={i}>{location}</MenuItem>
                                    ))}
                                </Select>
                            </FormControl> : <>
                                <TextField
                                    fullWidth
                                    label={`Add Delivery address`}
                                    type="text"
                                    value={deliveryDetails || ''}
                                    onChange={(e) => setDeliveryDetails(e.target.value)}
                                    className='!mt-10'
                                />
                                <div className='grid grid-cols-3 gap-2 mt-2'>
                                    <TextField
                                        fullWidth
                                        label={`City`}
                                        type="text"
                                        value={deliveryCity || ''}
                                        onChange={(e) => setDeliveryCity(e.target.value)}
                                    />

                                    <TextField
                                        fullWidth
                                        label={`State`}
                                        type="text"
                                        value={deliveryState || ''}
                                        onChange={(e) => setDeliveryState(e.target.value)}
                                    />

                                    <TextField
                                        fullWidth
                                        label={`Zip Code`}
                                        type="text"
                                        value={deliveryZipCode || ''}
                                        onChange={(e) => setDeliveryZipCode(e.target.value)}
                                    />
                                </div>
                                <div className='grid grid-cols-2 gap-2 w-full mt-3'>
                                    <TextField
                                        fullWidth
                                        label="Delivery Date"
                                        focused
                                        type="date"
                                        InputProps={{ style: { color: "#6b7280" } }}
                                        value={deliveryDate || ''}
                                        onChange={(e) => setDeliveryDate(e.target.value)}
                                    />
                                    <TextField
                                        fullWidth
                                        label="Delivery Time"
                                        focused
                                        type="time"
                                        InputProps={{ style: { color: "#6b7280" } }}
                                        value={deliveryTime || ''}
                                        onChange={(e) => setDeliveryTime(e.target.value)}
                                    />
                                </div>
                            </>}
                        </div>
                    </div>

                    <div className='mt-8 grid grid-cols-2 gap-6'>
                        <TextField
                            fullWidth
                            label="Return Date"
                            focused
                            type="date"
                            InputProps={{ style: { color: "#6b7280" } }}
                            value={returnDate || ''}
                            onChange={(e) => setReturnDate(e.target.value)}
                        />

                        <TextField
                            fullWidth
                            label="Return Time"
                            focused
                            type="time"
                            InputProps={{ style: { color: "#6b7280" } }}
                            value={returnTime || ''}
                            onChange={(e) => setReturnTime(e.target.value)}
                        />
                    </div>

                    <div className='mt-14'>
                        <h1 className='text-3xl font-medium'>How many luggage do you have?</h1>
                        <div className='grid grid-cols-10 gap-8 mt-6'>
                            <LuggageCounter
                                imgSrc={checkedIn}
                                count={checkedInCount}
                                onIncrement={() => setCheckedInCount(checkedInCount + 1)}
                                onDecrement={() => setCheckedInCount(Math.max(0, checkedInCount - 1))}
                                label="Checked in item"
                            />
                            <div className='col-span-4 flex items-center !pl-3'>
                                <h5 className='text-3xl font-bold relative pr-8'>Checked in item <span className='w-8 h-8 text-[1.5rem] bg-black rounded-full flex justify-center items-center absolute right-0 -top-3 font-semibold text-white overflow-hidden'>?</span></h5>
                            </div>
                        </div>
                        <div className='grid grid-cols-10 gap-8 mt-16'>
                            <LuggageCounter
                                imgSrc={carryon}
                                count={carryOnCount}
                                onIncrement={() => setCarryOnCount(carryOnCount + 1)}
                                onDecrement={() => setCarryOnCount(Math.max(0, carryOnCount - 1))}
                                label="Personal or Carryon item"
                            />
                            <div className='col-span-4 flex items-center !pl-3'>
                                <h5 className='text-3xl font-bold relative pr-8'>Personal or <br /> Carryon item
                                    <span className='w-8 h-8 text-[1.5rem] bg-black rounded-full flex justify-center items-center absolute right-2 top-4 font-semibold text-white overflow-hidden'>?</span>
                                </h5>
                            </div>
                        </div>
                        <div className='grid grid-cols-10 gap-8 mt-16'>
                            <LuggageCounter
                                imgSrc={oversized}
                                count={oversizedCount}
                                onIncrement={() => setOversizedCount(oversizedCount + 1)}
                                onDecrement={() => setOversizedCount(Math.max(0, oversizedCount - 1))}
                                label="Oversized Item"
                            />
                            <div className='col-span-4 flex items-center !pl-3'>
                                <h5 className='text-3xl font-bold relative pr-8'>Oversized Item
                                    <span className='w-8 h-8 text-[1.5rem] bg-black rounded-full flex justify-center items-center absolute right-0 -top-3 font-semibold text-white overflow-hidden'>?</span>
                                </h5>
                            </div>
                        </div>
                    </div>

                    {/* <div className="mt-14">
                        <h1 className='text-3xl font-medium'>Add Timing Details</h1>
                        <div className='mt-5 grid grid-cols-3 gap-10'>
                            <TextField
                                fullWidth
                                label="Return Date"
                                focused
                                type="date"
                                InputProps={{ style: { color: "#6b7280" } }}
                                value={returnDate || ''}
                                onChange={(e) => setReturnDate(e.target.value)}
                            />

                            <TextField
                                fullWidth
                                label="Return Time"
                                focused
                                type="time"
                                InputProps={{ style: { color: "#6b7280" } }}
                                value={returnTime || ''}
                                onChange={(e) => setReturnTime(e.target.value)}
                            />
                        </div>
                    </div> */}

                    <div className='flex justify-center mt-14 mb-3'>
                        <button
                            className='bg-[#FFDE59] px-12 py-3 rounded-lg text-2xl font-semibold text-[#1F1F1F] hover:bg-opacity-85 transition-opacity duration-500'
                            onClick={handleSubmitDetails}
                        >
                            Submit
                        </button>
                    </div>
                </div>
            </div>
        </section>
    );
};

export default Step3;


const Info = ({ onSelect, selected }) => (
    <div className='grid grid-cols-2 grid-rows-2 mt-4'>
        <button
            className={`px-5 py-3 flex flex-col hover:bg-yellow-200 transition-colors duration-200 border-l-8 border-gray-300 border-t-8 border-r-4 border-b-4 ${selected === 'Airport' ? 'bg-yellow-200' : ''}`}
            onClick={() => onSelect('Airport')}
        >
            <img src={airport} alt="airport" className='h-16 mx-auto' />
            <p className='text-center font-semibold text-lg mx-auto'>Airport</p>
        </button>
        <button
            className={`px-5 py-3 flex flex-col hover:bg-yellow-200 transition-colors duration-200 border-gray-300 border-r-8 border-b-4 border-l-4 border-t-8 ${selected === 'Home' ? 'bg-yellow-200' : ''}`}
            onClick={() => onSelect('Home')}
        >
            <img src={home} alt="home" className='h-16 mx-auto' />
            <p className='text-center font-semibold text-lg mx-auto'>Home</p>
        </button>
        <button
            className={`px-5 py-3 flex flex-col hover:bg-yellow-200 transition-colors duration-200 border-l-8 border-gray-300 border-b-8 border-r-4 border-t-4 ${selected === 'Hotel/Resort' ? 'bg-yellow-200' : ''}`}
            onClick={() => onSelect('Hotel/Resort')}
        >
            <img src={resort} alt="resort" className='h-16 mx-auto' />
            <p className='text-center font-semibold text-lg mx-auto'>Hotel/Resort</p>
        </button>
        <button
            className={`px-5 py-3 flex flex-col hover:bg-yellow-200 transition-colors duration-200 border-gray-300 border-r-8 border-t-4 border-l-4 border-b-8 ${selected === 'Office' ? 'bg-yellow-200' : ''}`}
            onClick={() => onSelect('Office')}
        >
            <img src={office} alt="office" className='h-16 mx-auto' />
            <p className='text-center font-semibold text-lg mx-auto'>Office</p>
        </button>
    </div>
)

const LuggageCounter = ({ imgSrc, count, onIncrement, onDecrement, label }) => (
    <div className='col-span-6 border-8 border-gray-300 flex'>
        <div className='w-[85%] px-3 flex justify-between items-center py-3'>
            <div className='w-[70%]'>
                <img src={imgSrc} alt={label} className='w-[70%]' />
            </div>
            <div className='flex flex-col text-3xl font-bold w-[30%]'>
                <p>Total</p>
                <p>Number</p>
                <div className='bg-[#FFDE59] w-16 h-16 rounded-full flex justify-center items-center text-[#EB2131] font-semibold text-5xl mt-2'>
                    {count}
                </div>
            </div>
        </div>
        <div className='w-[15%] border-l-8 border-gray-300 grid grid-rows-2'>
            <div className='border-b-8 border-gray-300 flex items-center justify-center'>
                <IconButton onClick={onIncrement}>
                    <Add className='text-[#EB2131] !text-6xl' />
                </IconButton>
            </div>
            <div className='flex items-center justify-center'>
                <IconButton onClick={onDecrement}>
                    <Remove className='text-[#EB2131] !text-6xl' />
                </IconButton>
            </div>
        </div>
    </div>
);