import React, { useState } from 'react';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import logo from "../../assets/logo.png";
import { google, apple, facebook } from '../../assets/loginSignup';
import { createTheme, TextField, ThemeProvider, Typography, CircularProgress } from '@mui/material';
import { toast, Bounce } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

const theme = createTheme({
  components: {
    MuiTextField: {
      styleOverrides: {
        root: {
          '& .MuiOutlinedInput-root': {
            backgroundColor: '#fff',
            '& fieldset': {
              borderColor: '#969B9E',
            },
            '&:hover fieldset': {
              borderColor: '#FFDE59',
            },
            '&.Mui-focused fieldset': {
              borderColor: '#FFDE59',
            },
          },
          '& .MuiInputLabel-root': {
            color: '#969B9E',
          },
          '&.Mui-focused .MuiInputLabel-root': {
            color: '#FFDE59',
          },
          '& .MuiFilledInput-input::placeholder': {
            color: '#969B9E',
          },
        },
      },
    },
  },
  palette: {
    primary: {
      main: '#FFDE59',
    },
  },
});

const Login = () => {
  const [loading, setLoading] = useState(false);
  const [formData, setFormData] = useState({ email: '', password: '' });
  const navigate = useNavigate();

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);

    try {
      const response = await axios.post('https://workbytech.host/luggage/api/login', formData, {
        headers: {
          'Content-Type': 'application/json',
          'X-CSRF-TOKEN': 'lAU5hI05NazejTBQ738cl8aAXCUygIhzbHG6jWla'
        }
      });

      if (response.data.status === 200) {
        localStorage.setItem('user', JSON.stringify(response.data.data.user));
        localStorage.setItem('token', response.data.data.token);

        toast.success('Account logged in successfully.', {
          position: "top-right",
          autoClose: 5000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
          theme: "light",
          transition: Bounce,
        });

        navigate('/');
      }
    } catch (error) {
      console.error('There was an error logging in!', error);
      toast.error('Login failed. Please check your credentials and try again.', {
        position: "top-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        theme: "light",
        transition: Bounce,
      });
    } finally {
      setLoading(false);
    }
  };

  return (
    <section className='py-4'>
      {loading && <div className='absolute top-0 left-0 h-[98vh] w-[98vw] flex justify-center items-center'><CircularProgress size={34} style={{ color: '#EB2131' }} /></div>}
      <img src={logo} alt="logo" className="h-16 drop-shadow-sm z-50 ml-6" />

      <div className='flex justify-center mt-16'>
        <div className='w-[35vw]'>
          <ThemeProvider theme={theme}>
            <h1 className='text-3xl font-semibold text-gray-700'>Welcome back!</h1>
            <form className='mt-12' onSubmit={handleSubmit}>
              <TextField
                id="outlined-basic"
                label="Email"
                variant="outlined"
                fullWidth
                name="email"
                value={formData.email}
                onChange={handleInputChange}
              />
              <TextField
                className='!mt-7'
                id="outlined-basic"
                label="Password"
                variant="outlined"
                fullWidth
                type="password"
                name="password"
                value={formData.password}
                onChange={handleInputChange}
              />
              <button
                className='rounded-lg w-full py-4 bg-[#EB2131] text-white cursor-pointer mt-10'
                type="submit"
                disabled={loading}
              >
                Log in
              </button>
            </form>
            <Typography className='text-gray-500 !mt-5 text-center'>
              <span>I forgot my password</span>
            </Typography>
            <Typography className='text-gray-500 !mt-5 text-center'>
              Don't have an account? <span className="text-[#EB2131]">Sign up.</span>
            </Typography>
          </ThemeProvider>

          <div className='relative text-center my-6'>
            <div className='border-[#cac7c3] border-[1px] absolute top-3 w-full'></div>
            <div className='bg-white text-[#cac7c3] inline-block font-medium px-3 relative'>Or</div>
          </div>

          <button className='rounded-md py-4 justify-center font-semibold text-lg mb-4 flex gap-2 items-center border-black border-[1px] w-full'> <img src={google} alt="google" className='h-5' /> Sign up with Google</button>
          <button className='rounded-md py-4 justify-center font-semibold text-lg mb-4 flex gap-2 items-center border-black border-[1px] w-full'> <img src={apple} alt="google" className='h-5' /> Sign up with Apple</button>
          <button className='rounded-md py-4 justify-center font-semibold text-lg flex gap-2 items-center border-black border-[1px] w-full'> <img src={facebook} alt="google" className='h-5' /> Sign up with Facebook</button>
        </div>
      </div>
    </section>
  );
}

export default Login;
