import React, { useEffect, useState } from "react";

import axios from "axios";
import Step1 from "./Step1";
import Step2 from "./Step2";
import Step3 from "./Step3";
import Step4 from "./Step4";
import Step5 from "./Step5";

const number =
  "h-9 w-9 flex justify-center items-center border-[#1F1F1F] border-2 rounded-full";
const line = "w-20 h-[2px] bg-[#1F1F1F]";

const BookingCmp = () => {

  const [showList, setShowList] = useState(false);
  const [showUser, setShowUser] = useState(false);
  const [showItem, setShowItem] = useState(false);
  const [showThankYou, setShowThankYou] = useState(false);
  const [step1, setStep1] = useState("");
  const [step2, setStep2] = useState(null);
  const [step3, setStep3] = useState({})
  const [locations, setLocations] = useState([]);
  const [locationList, setLocationList] = useState([]);
  const [zipCode, setzipCode] = useState("")

  const fetchLocations = async () => {
    try {
      // console.log(zipCode);
      const response = await axios.get(
        "https://secure.ontime360.com/sites/aafcouriers/api/locations",
        {
          params: { postalCode: zipCode },
          headers: {
            Accept: "application/json",
            Authorization: "4fe1b4ce-9dd4-4375-abb8-31bab61bd149",
          },
        }
      );
      // console.log(response.data);
      setLocations(response.data);
    } catch (error) {
      console.error("Error fetching locations:", error);
    }
  };

  useEffect(() => {
    const fetchLocationDetails = async () => {
      try {
        const locationDetailsPromises = locations.map((location) =>
          axios.get(
            `https://secure.ontime360.com/sites/aafcouriers/api/locations/${location}`,
            {
              headers: {
                Accept: "application/json",
                Authorization: "4fe1b4ce-9dd4-4375-abb8-31bab61bd149",
              },
            }
          )
        );

        const locationDetailsResponses = await Promise.all(
          locationDetailsPromises
        );

        locationDetailsResponses.forEach((res) => setLocationList(prev => {
          return [...prev, res?.data]
        }));
      } catch (error) {
        console.error("Error fetching location details:", error);
      }
    };

    if (locations.length > 0) {
      setLocationList([])
      fetchLocationDetails();
    }
  }, [locations]);

  return (
    <section className={`flex justify-center mt-36 pb-14`}>
      <div className="w-[50vw] flex flex-col items-center overflow-x-visible">
        <div className="flex items-center justify-center">
          <div className={`${number} bg-[#FFDE59]`}>1</div>
          <span className={`${line}`} />
          <div className={`${number} ${showUser && "bg-[#FFDE59]"}`}>2</div>
          <span className={`${line}`} />
          <div className={`${number} ${showList && "bg-[#FFDE59]"}`}>3</div>
          <span className={`${line}`} />
          <div className={`${number} ${showItem && "bg-[#FFDE59]"}`}>4</div>
          <span className={`${line}`} />
          <div className={`${number} ${showThankYou && "bg-[#FFDE59]"}`} >5</div>
        </div>

        <div className="mt-14 w-[80%]">
          {
            !showUser && !showThankYou ? (
              <Step1 setStep1={setStep1} setShowUser={setShowUser} />
            ) : showUser && !showList ? (
              <Step2 setShowList={setShowList} setStep2={setStep2} step1={step1} setZipCode={setzipCode} fetchLocations={fetchLocations} />
            ) : showList && !showItem ? (
              <Step3 locationList={locationList} setStep3={setStep3} setShowItem={setShowItem} />
            ) : showItem && !showThankYou ? (
              <Step4 step2={step2} step3={step3} setShowThankYou={setShowThankYou} />
            ) : showThankYou && <Step5 />
          }

        </div>
      </div>
    </section>
  );
};

export default BookingCmp;
